/**=====================
    Dashboard 1 css Start
==========================**/
.default-dashboard,
.ecommerce-dashboard,
.project-dashboard {
    table {
        tr {
            th {
                font-size: 15px;
                color: var(--light-font);
                font-weight: 500;
            }

            td {
                font-weight: 500;
            }

            &:last-child {
                td {
                    padding-bottom: 0 !important;
                }
            }

            th,
            td {
                &:first-child {
                    padding-left: unset !important;
                }

                &:last-child {
                    padding-right: unset !important;
                }
            }
        }
    }

    .datatable-wrapper {
        position: unset;

        .datatable-top {
            padding: 0;
            position: absolute;
            top: 14px;
            right: 48px;

            input {
                padding: 8px 12px;
                border: 0;
                background-color: var(--light-color);
                outline: 0;

                &::before {
                    content: 'f002';
                }

                @include media-breakpoint-up(lg) {
                    @include media-breakpoint-down(xxl) {
                        display: none;
                    }
                }
            }
        }

        .datatable-bottom {
            padding: 0;
            display: none;
        }

        .datatable-sorter {

            &:before,
            &:after {
                display: none;
            }
        }

        &.no-footer {
            .datatable-container {
                border-bottom: 0;
                border-top: 0;
            }
        }
    }

    .dropdown {
        &.icon-dropdown {
            position: absolute;
            right: 19px;
            top: 19px;

            .dropdown-toggle {
                border: none;
                background: transparent;
                color: $theme-body-sub-title-color;
                padding: 0;
                width: auto;

                i {
                    color: var(--light-font);
                }

                &::after {
                    display: none;
                }
            }
        }
    }
}

