            }
        }

        .datatable-wrapper {
            position: unset;

            .datatable-top {
                padding: 0;
                position: absolute;
                top: 14px;
                right: 48px;

                input {
                    padding: 8px 12px;
                    border: 0;
                    background-color: var(--light-color);
                    outline: 0;

                    &::before {
                        content: 'f002';
