/**=====================
    Icon css start
==========================**/
.icon-event{
	margin-bottom: -24px;
	.card{
		&:hover{
			transform: translateY(-5px);
			transition: 0.5s;
		}
	}
	.d-flex{
		padding: 20px 14px;
		text-align: center;
		transition: all 0.3s ease-in-out;
		@include media-breakpoint-between(lg , xl){
			padding: 18px 8px;
		}
		.flex-grow-1{
			margin-top: 14px;
		}
		i{
			[data-theme="dark"] & {
				color: var(--light-font);
				}
			font-size: 24px;
			&.flag-icon{
				font-size: 28px;
			}
		}
		h6{
			font-size: 15px;
			font-family: $body-font;
			color: var(--light-font);
			[data-theme="dark"] & {
				color: var(--light-font);
				}
		}
		&:hover{
			border-color: var(--theme-deafult)  !important;
			i{
				color: var(--theme-deafult);
			}
			svg{
				stroke: var(--theme-deafult);
				transition: all 0.2s ease-in-out;
			
			}
			h6{
				color: var(--theme-deafult);
				transition: all 0.3s ease-in-out;
			}
		}
	}
}
.icon-hover-bottom {
	background-color: $light;
	width: 100%;
	bottom: 0;
	left: 0;
	box-shadow: 0px 0px 1px 1px $light-color;
	z-index: 999;
	display: none;
	&.open{
		display: block;
	}
	.icon-popup {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;

		.close-icon {
			position: absolute;
			right: 10px;
			top: 10px;
			font-size: 20px;
			color: var(--gray-60);
			cursor: pointer;

			[dir="rtl"] & {
				right: unset;
				left: 10px;
			}
		}

		padding: 20px;

		>div {
			display: inline-block;

			.flag-icon {
				width: 60px;
				height: 60px;
			}
		}

		.icon-class {
			margin: 0 20px;
			cursor: pointer;
		}

		.icon-last {
			.form-inline {
				.form-group {
					display: initial;
				}
			}
		}
	}

	.icon-title {
		display: block;
		color: $dark-color;
		text-transform: uppercase;
		font-weight: 500;
	}

	.form-group {
		input {
			display: inline-block;
			width: 300px;
			background-color: $white;
		}
	}
}
.iconly-icons{
	.svg-menu{
		width: 24px;
		height: 24px;
		stroke: var(--body-font-color);
		[data-theme="dark"] & {
			stroke: var(--light-font);
			}
	}
}
#iconModal{
	.modal-body{
		pre{
			margin-bottom: 0;
		}
		.copy-text{
			position: absolute;
			top: 12px;
			right: 12px;
		}
	}
}
/**=====================
    Icon css end
==========================**/