/**========================
    67. Invoice CSS Start
=============================**/
.invoice {
  p {
    color: var(--light-text);
    text-transform: capitalize;
  }
  .invo-header {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid $light-gray;
  }
  .table-striped {
    tbody {
      tr {
        &:nth-of-type(odd) {
          background-color: rgba($primary-color, 0.05);
          --bs-table-accent-bg: unset;
        }
        h6 {
          text-transform: capitalize;
          color: var(--theme-deafult);
        }
      }
    }
  }
  .invo-profile {
    margin-bottom: 15px;
    .invo-profile-left {
      .d-flex {
        .flex-shrink-0 {
          margin-right: 15px;
        }
        .flex-grow-1 {
          h4 {
            font-size: 16px;
          }
          p {
            span {
              display: block;
            }
          }
        }
      }
    }
    .invo-profile-right {
      text-align: right;
      h6 {
        font-size: 16px;
      }
    }
  }
}
// invoice 5
.invoice-5 {
  .back {
    [data-theme="dark"] & {
      background: var(--card-color) !important;
    }
    td {
      padding-bottom: 15px !important;
      [data-theme="dark"] & {
        background: var(--card-color) !important;
      }
    }
  }
}
// invoice 4
.invoice-4 {
  .back {
    td {
      padding-bottom: 15px !important;
      [data-theme="dark"] & {
        background: var(--card-color) !important;
      }
    }
  }
  .padding{
    td{
      padding-bottom: 30px !important;
    }
  }
}
// invoice 2
.invoice-2 {
  .back {
    td {
      [data-theme="dark"] & {
        background: var(--card-color) !important;
      }
    }
  }
  .padding{
    td{
      padding-bottom: 18px !important;
    }
  }
}
@media only screen and (max-width: 1199px) {
  .invoice {
    h4 {
      margin-bottom: 0px;
    }
    h3 {
      margin-bottom: 0px;
    }
    .invo-profile {
      .d-flex {
        margin-bottom: 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid $light-gray;
      }
    }
    .invo-profile {
      .invo-profile-right {
        text-align: left;
      }
    }
  }
}
@media only screen and (max-width: 991px) {
  .invoice {
    .card {
      .card-body {
        padding: 20px;
      }
    }
    h4 {
      font-size: 20px;
    }
    h3 {
      font-size: 20px;
    }
    table {
      td {
        min-width: 150px;
        &:first-child {
          min-width: 280px;
        }
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .invoice {
    .invo-pal {
      margin-top: 15px;
      text-align: left !important;
    }
    .text-md-end {
      text-align: right;
    }
  }
}
@media only screen and (max-width: 575px) {
  .invoice {
    h4 {
      font-size: 18px;
    }
    h3 {
      font-size: 15px;
    }
    p {
      font-size: 13px;
    }
    .invo-header {
      .flex-grow-1 {
        text-align: right;
      }
    }
    .text-xs-center {
      margin-top: 20px;
      text-align: center !important;
    }
    .text-md-end {
      text-align: left;
    }
  }
}
@media only screen and (max-width: 420px) {
  .invoice {
    .invo-profile {
      .d-flex {
        .flex-grow-1 {
          margin-left: 0 !important;
          margin-top: 10px;
        }
      }
    }
  }
}
/**========================
      67. Invoice CSS endp
  ==========================**/